<template>
<v-container>
  <v-form @submit.prevent="agregarColaborador()">



    <v-text-field
      v-model="atributos.nombre"
      :counter="30"
      label="Nombre Colaborador"
    ></v-text-field>

    <v-text-field
      v-model="atributos.cedula"
      label="Cedula"
    ></v-text-field>

    <v-select
      v-model="atributos.entrada"
      :items="entradaO"
      label="Hora entrada"
    ></v-select>

    <v-select
      v-model="atributos.salida"
      :items="entrada2O"
      label="Hora salida"
    ></v-select>

    <v-btn
      color="success"
      class="mr-4"
      type="submit"      
    >
      Enviar registro
    </v-btn>

    <v-btn
      color="error"
      class="mr-4"      
    >
      Limpiar
    </v-btn>

    
  </v-form>
  </v-container>
</template>


<script>
export default {
  
data(){
  return{

    Atributos:[],

    atributos: {

      nombre:"",
      cedula:"",
      entrada: "",
      salida: "",




    },

    entradaO: ['1:00','2:00','3:00','4:00','5:00','6:00','7:00','8:00','9:00','10:00','11:00','12:00','13:00','14:00','15:00','16:00','17:00','18:00','19:00','20:00','21:00','22:00','23:00','24:00'],
    entrada2O: ['1:00','2:00','3:00','4:00','5:00','6:00','7:00','8:00','9:00','10:00','11:00','12:00','13:00','14:00','15:00','16:00','17:00','18:00','19:00','20:00','21:00','22:00','23:00','24:00'],



  }

  



},

methods:{


  agregarColaborador(){

    console.log(this.atributos);

    this.axios
    .post("/nuevo-registro", this.atributos)
    .then((res)=>{

      this.Atributos.push(res.data);
      this.atributos.nombre="";
      this.atributos.cedula="";
      this.atributos.entrada="";
      this.atributos.salida="";
      alert("Registro agregado satisfactoriamente");


      
    })
    .catch((e)=>{
      console.log(e.response);
      alert("Error en guardar registro")
    })




  }







}

}
</script>